export default {
	data() {
		return {
			// 控制轮播图的哪一项
			currentNum: 1,
			// 控制回到顶部显示与隐藏
			goTopStatus: false
		}
	},
	//监听页面高度(上滑或者下滑)
	onPageScroll(obj) {
		if (obj.scrollTop > 363) {
			return this.goTopStatus = true
		}
		this.goTopStatus = false
	},
	methods: {
		//轮播图切换时获取index
		getCurrentNum(e) {
			this.currentNum = e.detail.current
		},
		//点击回到顶部
		goToTop() {
			uni.pageScrollTo({
				scrollTop: 0
			})
		}
	}
}
